export const initialState = {
  multiple: 1,
  dealer: 0,
  pickup: 0,
  round: 0,
  small: 0,
  square: 0,
  radioOption: "dealer",
};
export const basicReducer = (state, action) => {
  let readString = "";
  switch (action.type) {
    case "incdec":
      return { ...state, ...action.val };
    case "changeRadio":
      return { ...state, radioOption: action.value };
    case "changeTextBox":
      return { ...state, ...action.value };
    case "reset":
      document.getElementById("rdealer").checked = true;
      return initialState;
    default:
      return state;
  }
};
