import React, { Component } from "react";

const Dropselector = () => {
  return (
    <div>
      <select name="cars" id="cars">
        <option value="talamban">Talamban</option>
        <option value="labangon">Labangon</option>
        <option value="kalimpio">Kalimpio</option>
      </select>
    </div>
  );
};

export default Dropselector;
